@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';

// &
.work_wrap {

	&.work_main {
		.showcase_main_banner_wrap {

			& + .section {
				margin-top: 50px;
			}
		}

		.showcase_template_wrap {
			.person_info_area {
				padding: 24px;
			}
		}
	}

	& > .section {
		&:first-child {
			margin-top: 0;
		}
	}

	.hot_people {
		padding: 40px 36px 49px 30px;
		border-radius: 20px;
		background-color: #fafaf8;

		.person_info_area {
			border: 0;
			padding: 0;

			.profile_info {
				margin-left: 30px;
			}
			.person_name {
				@include mixins.font_size_md;
			}
			.person_sub_name {
				@include mixins.font_size_xs;
			}
			.person_book_list {
				li {
					width: 100px;
				}
			}
		}
	}

	.work_main_list {
		position: relative;

		.person_info_area {
			padding: 0;
		}
		.swiper-button-prev {
			left: -64px;
		}
		.swiper-button-next {
			right: -64px;
		}
	}

	.section {
		margin-top: 70px;

		&.has_line {
			margin-top: 50px;
			padding-top: 50px;
			border-top: 1px solid variables.$colorGray04;
		}
	}

	.row_box {
		@include mixins.justify_width;
		flex-wrap: wrap;

		&.column_2 {
			.column {
				margin-left: 20px;
			}
		}
		&.column_5 {
			.column {
				margin-left: 33px;
			}
		}

		&[class*=column_] {
			.column:first-child {
				margin-left: 0;
			}
		}

		.column {
			flex: 0 1 auto;
		}
	}

	.prod_row_list {
		&.type_line {

			.prod_item {
				margin: 0;
				padding: 36px 0;
				border-bottom: 1px solid variables.$colorGray04;

				&:first-child {
					padding-top: 30px;
					border-top: 1px solid variables.$colorGray04;
				}
			}


		}
	}

	.person_info_wrap {
		overflow: hidden;
		border: 1px solid variables.$colorGray03;
		border-radius: 16px;

		.person_info_head {
			@include mixins.justify_width(center, between);
			padding: 18px 23px;
			background-color: variables.$colorGray06;

			.title_wrap {
				padding-bottom: 0;
			}
		}
		.person_info_area {
			border: 0;
			border-radius: 0;
		}
		.person_prod_box {
			margin-top: 9px;
		}
	}
	
	.person_info_area {
		padding: 40px 36px;

		.prod_badge {
			margin-bottom: 9px;
		}

		&.type_xl {
			.profile_img {
				width: 118px;
				height: 118px;
			}
			.person_name {
				@include mixins.font_size_sm;
			}
		}
	}
	.prod_area_wrap {
		@include mixins.justify_width(start, between);

		.prod_area  {
			flex: 1;
		}
		.prod_btn_wrap {
			width: 110px;
			margin-left: 50px;
			text-align: right;

			.btn_wrap {
				margin-top: 24px;
			}
		}
	}

	.top_cont_search_wrap {
		@include mixins.justify_width;
		margin-bottom: 24px;
	}

	.top_cont_search {
		@include mixins.justify_width;
		margin-left: auto;
		border-radius: 25px;
		background-color: variables.$colorGray06;

		.form_sel {
			width: 110px;

			.ui-selectmenu-button {
				height: 34px;
				border: 0;
				// border-radius: 25px 0 0 25px;
				background-color: transparent;
			}
		}
		.form_ip_search {
			width: 250px;
			// border-radius: 0 25px 25px 0;

			&:before {
				content: '';
				position: absolute;
				left: 0;
				top: 50%;
				height: 12px;
				border-left: 1px solid variables.$colorGray04;
				@include mixins.transform(translateY(-50%));
			}

			.form_ip {
				border: 0;
				background-color: transparent;
			}
		}

		&.type_white {
			background-color: #fff;
			border: 1px solid variables.$colorGray03;
		}
	}

	.prod_list_swiper {
		position: relative;

		.swiper-button-prev,
		.swiper-button-next {
			top: 130px;
		}
		.swiper-button-prev {
			left: -22px;
		}
		.swiper-button-next {
			right: -22px;
		}

		&.size_xl {
			.swiper-slide.prod_item {
				margin: 0;
			}
		}
	}

	.round_box {
		overflow: hidden;
		border-radius: 10px;
	}

	[class^=ico_] {
		display: inline-block;
		vertical-align: middle;
		transform: translateY(-1px);
	}
	.ico_bubble {
		width: 16px;
		height: 16px;
		@include mixins.ratio_images('../images/ico_bubble_16x16', 'png', 16px, 16px);
	}

	.picks_preference_author_wrap {
		@include mixins.ratio_images('../images/bg_picks_preference_author_wrap_01', 'png', auto, 254px);
		background: {
			repeat: no-repeat;
			position: left top;
		}

		.person_profile_box {
			flex: 1;
		}
		.person_etc {
			position: absolute;
			right: 20px;
			top: 20px;
		}
		.btn_share {
			margin-left: 3px;
			background-color: #fff;
		}

		& + .section {
			margin-top: 50px;
		}
	}
}

//  
.campain_wrap {

	.campain_head {
		position: relative;
		margin-bottom: 30px;
		text-align: center;
	
		.title_wrap {
			padding-bottom: 0;
			justify-content: center;
		}
		.desc {
			margin-top: 10px;
			@include mixins.font_size_sm;
			color: variables.$textColorGray;
		}
		.sns_share_wrap {
			position: absolute;
			right: 0;
			top: 0;
		}
	}

	.round_purple_box {
		padding: 40px 50px 50px;
		border-radius: 20px;
		background-color: rgba(80, 85, 177, .06);

		.head {
			text-align: center;

			[class^=ico_] {
				margin-bottom: 10px;
			}
			.title_wrap {
				justify-content: center;
				padding-bottom: 0;
			}
			
			.desc {
				margin-top: 10px;
				color: variables.$textColorGray;
			}
		}

		.text_check {
			width: 645px;
			margin: 20px auto 0;

			li {
				position: relative;
				margin-top: 30px;
				padding-left: 28px;
				color: variables.$textColorGray;

				&:first-child {
					margin-top: 0;
				}

				&:before {
					content: '';
					position: absolute;
					left: 0;
					top: 50%;
					width: 18px;
					height: 15px;
					@include mixins.transform(translateY(-50%));
					@include mixins.ratio_images('../images/ico_check_18x15', 'png', 18px, 15px);
				}
			}
		}

		& + .round_gray_box {
			margin-top: 20px;
		}
	}

	.section {
		margin-top: 70px;
		&:first-child {
			margin-top: 0;
		}
	
		.head {
			margin-bottom: 30px;
			text-align: center;
	
			[class^=ico_] {
				margin-bottom: 10px;
			}

			.award {
				@include mixins.justify_width(center, center);

				&.has_line {
					&:before,
					&:after {
						content: '';
						flex: 1;
						height: 1px;
						background-color: variables.$colorGray04;
					}
				}

				.img {
					width: 133px;
					height:73px;
					margin: 0 20px;

					img {
						max-width: 100%;
					}
				}
			}
			.title_wrap {
				justify-content: center;
				padding-bottom: 0;
			}
			.title_wrap.title_size_def {
				margin-top: 24px;
			}
			.desc {
				margin-top: 10px;
				color: variables.$textColorGray;
			}
			.bul_item_asterisk {
				margin-top: 6px;
			}
			.btn_wrap {
				margin-top: 20px;

				[class^=btn_] {
					margin-left: 10px;

					&:first-child {
						margin-left: 0;
					}
				}
			}
		}

		.round_gray_box {
			&.campain {
				@include mixins.justify_width(center, center);
				margin-top: 70px;

				[class^=ico_] {
					margin-right: 10px;
				}
				.desc {
					@include mixins.font_size_def;
					font-weight: variables.$fontWeightBold;
				}
			}
		}

		.comment_wrap .comment_list .comment_item {
			border-top: 1px solid variables.$colorGray04;
		}
	}

	.tab_wrap {
		&.type_fill {
			.tab_list_wrap {
				margin-bottom: 70px;
			}
		}
		&.type_tag {
			.tab_list_wrap {
				margin-bottom: 70px;
			}
			.tabs {
				@include mixins.justify_width(center, center);
			}
		}

		&.type_line_round.type_icon_box {
			.tab_list_wrap {
				margin-bottom: 50px;
				justify-content: center;

				.tab_item {
					margin-left: 20px;
					border-color: #d5d5d5;
					border-radius: 10px;
					background-color: variables.$colorGray06;

					&:first-child {
						margin-left: 0;
					}
				}
				.tab_link {
					box-sizing: border-box;
					@include mixins.justify_width(center, center, column);
					width: 130px;
					height: 130px;
					padding: 0;

					.tab_text {
						margin-top: 6px;
						@include mixins.font_size_sm;
					}
				}

				.ui-state-active {
					background: #fff;
					.tab_link {
						&:after {
							border-width: 2px;
						}

						.tab_text {
							font-weight: variables.$fontWeightBold;
						}
					}
				}
			}
			
		}
	}

	[class^=ico_] {
		display: inline-block;
		background: {
			repeat: no-repeat;
			position: center center;
		}
	}
	[class^=ico_lg] {
		width: 46px;
		height: 46px;
	}
	
	.ico_lg_pen {
		@include mixins.ratio_images('../images/ico_pen_46x46', 'png', 46px, 46px);
	}
	.ico_lg_notify {
		@include mixins.ratio_images('../images/ico_notify_46x46', 'png', 46px, 46px);
	}
	.ico_lg_flag {
		@include mixins.ratio_images('../images/ico_flag_46x46', 'png', 46px, 46px);
	}
	.ico_lg_comment {
		@include mixins.ratio_images('../images/ico_comment_46x46', 'png', 46px, 46px);
	}
	.ico_lg_check {
		@include mixins.ratio_images('../images/ico_check_46x46', 'png', 46px, 46px);
	}
	.ico_lg_award {
		@include mixins.ratio_images('../images/ico_award_46x46', 'png', 46px, 46px);
	}
	.ico_lg_show {
		@include mixins.ratio_images('../images/ico_show_46x46', 'png', 46px, 46px);
	}
	.ico_lg_down {
		@include mixins.ratio_images('../images/ico_down_46x46', 'png', 46px, 46px);
	}
	.ico_lg_font {
		@include mixins.ratio_images('../images/ico_font_46x46', 'png', 46px, 46px);
	}

	[class^=ico_md] {
		width: 30px;
		height: 30px;
	}
	.ico_md_child {
		@include mixins.ratio_images('../images/ico_stage_01_30x30', 'png', 30px, 30px);
	}
	.ico_md_teenager {
		@include mixins.ratio_images('../images/ico_stage_02_30x30', 'png', 30px, 30px);
	}
	.ico_md_general {
		@include mixins.ratio_images('../images/ico_stage_03_30x30', 'png', 30px, 30px);
	}
	.ui-state-active {
		.ico_md_child {
			@include mixins.ratio_images('../images/ico_stage_01_active_30x30', 'png', 30px, 30px);
		}
		.ico_md_teenager {
			@include mixins.ratio_images('../images/ico_stage_02_active_30x30', 'png', 30px, 30px);
		}
		.ico_md_general {
			@include mixins.ratio_images('../images/ico_stage_03_active_30x30', 'png', 30px, 30px);
		}
	}

	[class^=ico_sm] {
		width: 24px;
		height: 24px;
	}
	.ico_sm_pen {
		@include mixins.ratio_images('../images/ico_pen_24x24', 'png', 24px, 24px);
	}
	.ico_sm_doc {
		@include mixins.ratio_images('../images/ico_doc_24x24', 'png', 24px, 24px);
	}
	.ico_sm_people {
		@include mixins.ratio_images('../images/ico_people_24x24', 'png', 24px, 24px);
	}

	.ico_cp_next {
		width: 24px;
		height: 12px;
		@include mixins.ratio_images('../images/ico_next_24x12', 'png', 24px, 12px);
	}
	.ico_cp_insta {
		width: 50px;
		height: 50px;
		@include mixins.ratio_images('../images/ico_insta_50x50', 'png', 50px, 50px);
	}
	.ico_btn_check {
		width: 13px;
		height: 9px;
		@include mixins.ratio_images('../images/ico_check_13x9', 'png', 13px, 9px);
	}

}

//  
.campain_year_list {
	overflow: hidden;
	position: absolute;
	right: 0;
	top: 24px;
	padding: 24px 10px 24px 24px;
	border-radius: 20px 20px 20px 0;
	background-color: rgba(0, 0, 0, .16);

	.head {
		margin-bottom: 24px;
		.title_wrap {
			padding-bottom: 0;
		}
	}

	.custom_scroll_wrap {
		width: 193px;
		height: 297px;

		.simplebar-track {
			background: rgba(0, 0, 0, .16);

			&.simplebar-vertical .simplebar-scrollbar:before {
				background-color: #fff;
			}
		}
		
	}

	.list {
		li {
			margin-top: 14px;
			padding-top: 14px;
			border-top: 1px solid rgba(255, 255, 255, .1);

			&:first-child {
				margin-top: 0;
				padding-top: 0;
				border-top: none;
			}
		}

		.item {
			@include mixins.justify_width(center, start);
		}
		.img {
			overflow: hidden;
			width: 40px;
			height: 40px;
			border-radius: 50%;
			margin-right: 8px;

			img {
				max-width: 100%;
			}
		}
		.text {
			overflow: hidden;
			position: relative;
			@include mixins.font_size_xxs;
			padding-right: 13px;
			font-weight: variables.$fontWeightBold;
			color: #fff;
			white-space: nowrap;
			text-overflow: ellipsis;

			&:after {
				content: '';
				position: absolute;
				right: 0;
				top: 2px;
				width: 7px;
				height: 7px;
				@include mixins.ratio_images('../images/ico_link_7x7', 'png', 7px, 7px);
			}
		}
	}
}

// 
.campain_content {
	ul {
		@include mixins.justify_width(start, center);
	}
	li {
		width: 300px;
		margin-left: 50px;

		&:first-child {
			margin-left: 0;
		}
	}
	.item {
		@include mixins.justify_width(center, start, column);
		text-align: center;

		.img {
			overflow: hidden;
			border-radius: 20px;

			&.border {
				border: 1px solid variables.$colorGray04;
			}

			img {
				max-width: 100%;
			}
		}

		.title_wrap {
			margin-top: 20px;
			padding-bottom: 0;
		}
		.desc {
			margin: 10px 0 12px;
		}
	}
}

//  
.campain_partner {
	ul {
		@include mixins.justify_width(center, between);
	}
	li {
		box-sizing: border-box;
		width: 394px;
		padding: 20px;
		border: 1px solid variables.$colorGray04;
		border-radius: 10px;
		text-align: center;
	}
	.sort {
		@include mixins.font_size_sm;
		font-weight: variables.$fontWeightBold;
		color: variables.$textColorLightGray;
	}
	.desc {
		margin-top: 7px;
	}
}

//  
.campain_effect_info {
	ul {
		@include mixins.justify_width(start, between);
	}
	li {
		width: 273px;
		text-align: center;
	}
	.img {
		overflow: hidden;
		width: 273px;
		height: 273px;
		border-radius: 20px;

		img {
			max-width: 100%;
		}
	}
	.desc {
		margin-top: 20px;
		@include mixins.font_size_sm;
	}
}

// 
.campain_people_list {
	ul {
		@include mixins.justify_width;
		flex-wrap: wrap;
	}
	li {
		width: 590px;
		margin: 20px 20px 0 0;

		&:nth-child(-n+2) {
			margin-top: 0;
		}
		&:nth-child(2n) {
			margin-right: 0;
		}
	}

	.person_info_area {
		display: block;

		.person_category .category {
			color: variables.$textColorLightGray;
		}

		.person_name {
			padding: 0;
			@include mixins.justify_width(center, start);
		}

		.badge {
			display: inline-block;
			box-sizing: border-box;
			min-width: 50px;
			height: 25px;
			margin-left: 5px;
			padding: 0 8px;
			border-radius: 13px 13px 13px 0;
			@include mixins.font_size_min;
			line-height: 25px;
			font-weight: variables.$fontWeightRegular;
			background-color: variables.$kyoboPurple;
			color: #fff;
		}

		.person_desc {
			@include mixins.font_size_xs;
		}
	}
}

//  
.campain_apply_info {
	ul {
		@include mixins.justify_width(start, between);
	}
	.item {
		overflow: hidden;
		@include mixins.justify_width(center, center);
		width: 387px;
		height: 84px;
		border-radius: 10px;
		background-color: rgba(variables.$kyoboPurple, .06);
		cursor: pointer;

		[class^=ico_sm] {
			margin-right: 6px;
		}

		.text {
			position: relative;
			@include mixins.font_size_def;
			font-weight: variables.$fontWeightBold;
			padding-right: 14px;

			&:after {
				content: '';
				position: absolute;
				right: 0;
				top: 50%;
				width: 4px;
				height: 10px;
				@include mixins.transform(translateY(-50%));
				@include mixins.ratio_images('../images/ico_arr_4x10', 'png', 4px, 10px);
				background: {
					repeat: no-repeat;
					position: center;
				}
			}
		}
	}

	& + .btn_wrap.page_bottom {
		margin-top: 70px;
	}
}

//   
.campain_apply_write {
	.tbl_row_wrap {
		& + .info_text_box {
			margin-top: 10px;
		}
	}

	.info_text_box {
		.btn_more_view {
			margin-left: 9px;
		}
	}

	.img_preview {
		overflow: hidden;
		display: inline-block;
		border-radius: 10px;

		img {
			max-width: 100%;
		}
	}
}

//  
.campain_award_box {
	ul {
		@include mixins.justify_width(start, between);
	}
	li {
		box-sizing: border-box;
		@include mixins.justify_width(center, start, column);
		width: 285px;
		height: 270px;
		padding: 30px;
		border: 1px solid variables.$colorGray04;
		border-radius: 16px;
		text-align: center;
	}
	.img {
		width: 114px;
		height: 114px;
		
		img {
			max-width: 100%;
		}
	}
	.title_wrap {
		margin-top: 20px;
		padding-bottom: 0;
	}
	.desc {
		margin-top: 10px;
	}
}

//  
.campain_audit_info {
	.bul_item_asterisk {
		margin-top: 6px;
	}
	.info_text_box {
		margin-top: 12px;
	}
}

// 
.campain_winner_box {
	@include mixins.justify_width(start, between);

	[class^=round_] {
		box-sizing: border-box;
		flex: 1;
		flex-direction: column;
		align-self: stretch;
		margin: 0 10px;
		padding: 40px;
		text-align: center;

		&:first-child {
			margin-left: 0;
		}
		&:last-child {
			margin-right: 0;
		}

		.title_wrap {
			justify-content: center;
			padding-bottom: 0;
		}
		.list {
			margin-top: 20px;

			&:first-child {
				margin-top: 0;
			}

			li {
				margin-top: 11px;
				@include mixins.font_size_sm;

				&:first-child {
					margin-top: 0;
				}
			}
		}
	}
}

// 
.campain_judge_circle {
	& + .round_gray_box {
		margin-top: 20px;
	}

	ul {
