@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';
$templateHeight: 343px; //   1 min-height
$templateHeightLg: 673px; //   2 min-height

@import 'service_cjh';

/*    */
.showcase_main_banner_wrap {
	@include mixins.justify_width();
	align-items: stretch;
	width: 100%;

	.showcase_main_banner_area {
		flex: 1 0;
		width: 100%;
		height: 100%;
		overflow: hidden;

		.tab_wrap {
			position: relative;
			padding-bottom: 54px;
			height: 394px;
			box-sizing: border-box;
			overflow: hidden;

			.tab_list_wrap {
				position: absolute;
				bottom: 0;
				left: 0;
				height: 54px;
				margin-bottom: 0;

				.tabs {
					.tab_item {
						.tab_link {
							.tab_text {
								@include mixins.font_size_xs;

								.fc_spot {
									display: none;
								}
							}

							&:after {
								height: 2px;
							}
						}

						&.ui-state-active {
							.tab_link {
								.tab_text {
									.fc_spot {
										display: inline;
									}
								}
							}
						}
					}
				}
			}
		}

		// swiper
		.showcase_main_swiper_wrap {
			display: block;
			height: 340px;
			border-radius: 30px 0 30px 30px;
			overflow: hidden;

			.showcase_banner_box {
				display: block;

				.showcase_banner_link {
					display: block;
					width: 100%;
				}

				img {
					width: 100%;
				}
			}
		}

		.swiper_control_floating_box {
			.swiper-button-prev {
				left: 36px;
			}

			.swiper-button-next {
				right: 36px;
			}
		}
	}

	.showcase_main_event_area {
		flex: 0 auto;
		width: 170px;
		height: 100%;
		margin-left: 20px;

		.showcase_event_box {
			display: block;
			border-radius: 16px;
			overflow: hidden;

			.showcase_event_link {
				display: block;
				width: 100%;
			}

			img {
				width: 100%;
			}
		}
	}

	+ .showcase_template_wrap, + .tab_wrap.type_line {
		margin-top: 50px;
	}
}

/*     */
.showcase_banner_swiper_wrap{
	overflow: hidden;
	border-radius: 20px;
	margin-bottom: 50px;

	.showcase_banner_box{

		img{
			width: 100%;
		}
	}
}

/*     */
.showcase_blurb_wrap {
	font-size: 0;
	margin-top: 30px;

	.showcase_blurb_box {
		overflow: hidden;
		display: inline-block;
		width: calc((100% - 12px) / 3);
		border-radius: 10px;
		vertical-align: top;

		.blurb_link {
			display: block;
			height: 100%;
		}

		img {
			width: 100%;
		}

		~ .showcase_blurb_box {
			margin-left: 6px;
		}
	}

	+ .showcase_template_wrap, + .tab_wrap.type_line {
		margin-top: 50px;
	}
}

/*   B    */
.showcase_template_wrap {
	//  b  
	.template_row {
		@include mixins.justify_width;
		align-items: stretch;

		.template_col {
			flex: 1;
			max-width: 984px;
			//overflow: hidden;

			.tab_wrap {
				&.type_line {
					> .tab_list_wrap {
						margin-bottom: 20px;
					}
				}
			}

			.tag_wrap {
				width: 100%;
			}

			.round_gray_box {
				&.showcase_keyword {
					border-radius: 16px;
				}
			}

			+ .template_col {
				margin-left: 36px;
			}

			&.no_title {
				padding-top: 57px;
			}

			&.col2 {
				flex: 0 1 auto;
				width: calc((((100% - (36 * 11px)) / 12) * 8) + (36 * 7px));

				.prod_list {
					&:not(.gray_box){
						.prod_item {
							margin-left: calc((100% - (#{map.get(variables.$prodProps, 'thumb-v-def')} * 4)) / 3);

							&:nth-child(4n+1) {
								margin-left: 0;
							}

							&:nth-child(n+5) {
								margin-top: 24px;
							}
						}
					}

					&.type_round{
						.prod_item {
							&:nth-child(5n+1) {
								&:before{
									content: none;
								}
							}

							&:nth-child(4n+1) {
								position: relative;

								&:before{
									content: '';
									position: absolute;
									top: 0;
									left: 0;
									width: 644px;
									height: 1px;
									background-color: variables.$colorGray04;
								}
							}

							&:first-child{
								&:before{
									content: none;
								}
							}

							&:nth-child(n+5) {
								margin-top: 36px;
								padding-top: 37px;
							}
						}
					}

					&.gray_box{
						margin-left: calc((100% - (201px * 3)) / 2);

						&:nth-child(3n+1) {
							margin-left: 0;
						}

						&:nth-child(n+4) {
							margin-top: 24px;
						}
					}
				}

				.tab_wrap {
					&.type_line {
						.tab_content {
							padding: 0 8px;
						}
					}
				}

				.publisher_event_list {
					.publisher_event_item {
						width: calc((100% - 16px) / 2);

						&:nth-child(3n+1) {
							margin-left: 16px;
						}

						&:nth-child(2n+1) {
							margin-left: 0;
						}

						&:nth-child(n+3) {
							margin-top: 16px;
						}
					}
				}

				.prod_line_box {
					&.col2 {
						.col {
							.prod_area {
								padding-bottom: 30px;

								.prod_thumb_box {
									width: 118px;

									.img_box{
										img{
											max-height: calc(118px * 1.618 - 10px);
										}
									}
								}
							}

							.event_info {
								margin-top: auto;
							}

							&:first-child {
								padding-right: 24px;
							}

							&:last-child {
								padding-left: 24px;
							}
						}

						// POD  >   POD (     + _2x1)
						.expand_related_book_wrap {
							position: relative;
							align-self: center;
							padding-right: 184px;
							margin: 0 10px;

							.detail_swiper_wrap {
								width: 380px;
								padding-right: 40px;

								.prod_area {
									.prod_thumb_box {
										width: 142px;

										.img_box{
											img{
												max-height: calc(142px * 1.618 - 10px);
											}
										}
									}
								}
							}

							.thumb_swiper_wrap {
								width: 284px;

								.thumb_list {
									.thumb_item {
										width: 84px;
										margin-right: 16px;

										&:last-child {
											margin-right: 0;
										}
									}
								}
							}

							.swiper-button-prev {
								left: -63px;
							}
							.swiper-button-next {
								right: -63px;
							}
						}
					}
				}

				.best_prod_wrap {
					.prod_row_list {
						font-size: 0;

						.prod_item {
							display: inline-block;
							width: calc((100% - 36px) / 2);
							vertical-align: top;

							.prod_area {
								align-items: flex-start;

								.prod_info_box {
									.badge_flag {
										margin-top: 10px;
									}
								}
							}

							+ .prod_item {
								margin-top: 0;
							}

							&:nth-child(n+3) {
								margin-top: 20px;
							}

							&:nth-child(2n) {
								margin-left: 36px;
							}
						}
					}

					.more_best_rank_box{
						.best_rank_list{
							@include mixins.justify_width(start, start, column);
							flex-wrap: wrap;
							height: 124px;
							font-size: 0;

							.best_rank_item{
								width: calc((100% - 36px) / 2);

								&:nth-child(5){
									margin-top: 0;
								}

								&:nth-child(n+5){
									margin-left: 36px;
								}
							}
						}
					}
				}
			}

			&.col1 {
				flex: 0 1 auto;
				width: calc((((100% - (36 * 11px)) / 12) * 4) + (36 * 3px));

				.prod_list {
					.prod_item {
						margin-left: calc((100% - (#{map.get(variables.$prodProps, 'thumb-v-def')} * 2)));

						&:nth-child(2n+1) {
							margin-left: 0;
						}

						&:nth-child(n+3) {
							margin-top: 24px;
						}
					}
				}

				.prod_line_list {
					.prod_line_item {
						width: 100%;
						margin-left: 0;

						+ .prod_line_item {
							margin-top: 20px;
						}
					}
				}

				.publisher_event_list {
					.publisher_event_item {
						width: 100%;
						margin-left: 0;

						~ .publisher_event_item {
							margin-top: 16px;
						}
					}
				}

				.round_gray_box{
					&.showcase_keyword{
						min-height: 673px;
						border-radius: 16px;
					}
				}

				.tag_wrap{
					.custom_scroll_wrap{
						max-height: 343px;
					}
				}
			}
		}

		~ .template_row {
			margin-top: 70px;
		}
	}

	//  b  
	.title_wrap {
		height: 44px;
		padding-bottom: 13px;

		&.has_sub_title {
			height: auto;
		}
	}

	//   
	.prod_list {
		font-size: 0;

		.prod_item {
			display: inline-block;
			width: map.get(variables.$prodProps, 'thumb-v-def');
			margin-left: calc((100% - (#{map.get(variables.$prodProps, 'thumb-v-def')} * 6)) / 5);
			vertical-align: top;

			&:nth-child(6n+1) {
				margin-left: 0;
			}

			&:nth-child(n+7) {
				margin-top: 24px;
			}
		}

		&.col5 {
			.prod_item {
				width: map.get(variables.$prodProps, 'thumb-v-lg');
				margin-left: calc((100% - (#{map.get(variables.$prodProps, 'thumb-v-lg')} * 5)) / 4);

				&:nth-child(5n+1) {
					margin-left: 0;
				}

				&:nth-child(n+6) {
					margin-top: 24px;
				}
			}

			&.type_round{
				.prod_item {
					&:nth-child(5n+1) {
						position: relative;

						&:before{
							content: '';
							position: absolute;
							top: 0;
							left: 0;
							width: 984px;
							height: 1px;
							background-color: variables.$colorGray04;
						}
					}

					&:first-child{
						&:before{
							content: none;
						}
					}

					&:nth-child(n+6) {
						padding-top: 25px;
					}
				}
			}
		}

		&.col2{
			.prod_item {
				margin-left: calc((100% - (#{map.get(variables.$prodProps, 'thumb-v-def')} * 2)));

				&:nth-child(2n+1) {
					margin-left: 0;
				}

				&:nth-child(n+3) {
					margin-top: 24px;
				}
			}
		}

		//   
		&.gray_box {
			.prod_item{
				width: 201px;
				margin:{
					left: 0;
					right: 20px;
				}

				.prod_area{
					padding: 30px 27px;
					border-radius: 16px;
					background-color: variables.$colorGray06;
					box-sizing: border-box;

					.prod_thumb_box {
						width: 116px;
						height: 116px;
						margin: 0 auto;
					}
				}

				&:last-child{
					margin-right: 0;
				}
			}
		}
	}

	//   
	.prod_row_list{
		//   
		&.gray_box {
			.prod_item{
				.prod_area{
					height: 160px;
					border-radius: 16px;
					background-color: variables.$colorGray06;
					box-sizing: border-box;

					&.horizontal{
						padding: 0 24px;

						.prod_thumb_box {
							width: 88px;
							height: 88px;

							.prod_link {

							}
						}
					}

				}

				~ .prod_item{
					margin-top: 16px;
				}
			}
		}
	}

	//  
	.prod_list{
		.prod_item{
			&:nth-child(n+7) {
				margin-top: 24px;
			}
		}
	}

	//   ,
	.prod_line_box {
		@include mixins.justify_width(center, center, column);
		height: calc(100% - 57px);
		border-radius: 20px;
		border: 1px solid variables.$colorGray03;
		min-height: $templateHeight;
		padding: 29px;
		box-sizing: border-box;
		background-color: #fff;

		.prod_summary {
			max-width: 100%;
			overflow: hidden;
			@include mixins.justify_width;
			margin-bottom: 20px;
			text-align: center;

			.desc {
				@include mixins.ellipsis(false);
				@include mixins.font_size_xxs;
				color: rgba(variables.$textColorBlack, .8);
			}

			&:before, &:after {
				content: '';
				flex-shrink: 0;
				display: inline-block;
				width: 10px;
				height: 9px;
				vertical-align: top;
			}

			&:before {
				margin-right: 8px;
				@include mixins.ratio_images('../images/ico_prod_introduce_desc', 'png', 10px, 9px);
			}

			&:after {
				margin-left: 8px;
				@include mixins.ratio_images('../images/ico_prod_introduce_desc_reverse', 'png', 10px, 9px);
			}
		}

		.event_info {
			width: 100%;
			margin-top: 40px;
			padding: 16px 18px 18px;
			border-radius: 10px;
			background-color: rgba(variables.$kyoboPurple, .06);
			box-sizing: border-box;

			.event_period {
				@include mixins.font_size_xxs;
				font-weight: variables.$fontWeightMedium;
				color: variables.$textColorPurple;
				text-align: center;

				&:before {
					content: '';
					display: inline-block;
					width: 15px;
					height: 15px;
					margin: 2px 5px 0 0;
					@include mixins.ratio_images('../images/ico_event_period', 'png', 15px, 15px);
					vertical-align: top;
				}
			}

			.event_desc {
				margin-top: 4px;
				@include mixins.font_size_xxs;
				color: variables.$textColorGray;
				font-weight: variables.$fontWeightMedium;
				text-align: center;
			}
		}

		.prod_area{
			width: 100%;

			.prod_info_box{
				.prod_price{
					&.type_cut {
						.price_info {
							.price_normal {
								margin-left: 0;
								margin-right: 5px;
							}
						}

					}
				}
			}
		}

		.prod_row_list {
			width: 100%;
			
			&.has_line {
				.prod_item {
					& ~ .prod_item {
						margin-top: 30px;
						padding-top: 30px;
					}
				}
			}
		}

		&:not(.col2) {
			max-width: 304px;

			.prod_area {
				margin: 0 auto;

				.prod_thumb_box {
					margin: 0 auto;
				}

				.prod_info_box {
					text-align: center;
				}
			}
		}

		&.size_h_lg {
			min-height: $templateHeightLg;

			.prod_summary {
				margin-bottom: 30px;
			}
		}

		&.col2 {
			align-items: stretch;
			flex-direction: row;
			font-size: 0;

			.col {
				@include mixins.justify_width(center, center, column, true);
				height: 100%;
				width: 50%;
				box-sizing: border-box;
				min-height: 283px;

				.event_info {
					.event_period, .event_desc {
						text-align: left;
					}

					.event_desc {
						margin-left: 20px;
					}
				}

				.prod_area {
					.prod_info_box {
						.prod_introduction {
							@include mixins.font_size_xs;
							@include mixins.multi_ellipsis(variables.$fontSizeXsLineHeight, 3, false);
						}
					}
				}

				&:first-child {
					padding-right: 30px;
				}

				&:last-child {
					padding-left: 30px;
					border-left: 1px solid variables.$colorGray04;
				}
			}
		}

		@at-root .title_wrap.has_sub_title + .prod_line_box{
			height: calc(100% - 82px);
		}
	}

	// prod_line_box 
	.prod_line_list {
		@include mixins.justify_width;
		align-items: stretch;
		flex-wrap: wrap;

		.prod_line_item {
			display: inline-block;
			width: calc((100% - 36px) / 3);
			vertical-align: top;

			.prod_line_box {
				max-width: none;
				height: 100%;
			}

			&:nth-child(n+4) {
				margin-top: 20px;
			}

			&:nth-child(3n+1) {
				margin-left: 0;
			}

			+ .prod_line_item {
				margin-left: 18px;
			}
		}
	}

	//  
	.publisher_event_list {
		font-size: 0;

		.publisher_event_item {
			display: inline-block;
			width: calc((100% - 32px) / 3);
			border-radius: 10px;
			border: 1px solid variables.$colorGray03;
			vertical-align: top;
			box-sizing: border-box;

			.publisher_event_link {
				display: block;
				@include mixins.justify_width(center);
				height: 100%;
				padding: 13px;
				box-sizing: border-box;
			}

			~ .publisher_event_item {
				margin-left: 16px;
			}

			&:nth-child(3n+1) {
				margin-left: 0;
			}

			&:nth-child(n+4) {
				margin-top: 16px;
			}
		}

		.publisher_event_area {

			.event_thumb_box {
				flex-shrink: 0;
				margin-left: auto;
				width: 46px;
				height: 70px;

				img {
					display: block;
					width: 100%;
					height: 100%;
				}
			}

			.event_info_box {
				overflow: hidden;
				margin-right: 24px;

				.event_name {
					@include mixins.multi_ellipsis(variables.$fontSizeXxsLineHeight, 2, false);
					@include mixins.font_size_xxs;
					font-weight: variables.$fontWeightBold;
				}

				.event_desc {
					@include mixins.ellipsis(false);
					margin-top: 8px;
					@include mixins.font_size_min;
					color: variables.$textColorGray;
				}
			}
		}
	}

	//  
	.best_prod_wrap {
		.more_best_rank_box {
			margin-top: 24px;
			padding-top: 24px;
			border-top: 1px solid variables.$colorGray04;
		}

		.best_rank_list {
			.best_rank_item {
				@include mixins.justify_width;
				overflow: hidden;

				.badge_flag {
					flex-shrink: 0;
					margin-right: 8px;
				}

				.prod_name {
					@include mixins.ellipsis(false);
					@include mixins.font_size_xxs;
				}

				~ .best_rank_item {
					margin-top: 16px;
				}
			}
		}

		.prod_area {
			.prod_info_box {
				.review_summary_wrap {
					margin-top: 8px;
				}
			}
		}
	}

	//  _01_3x1
	.expand_related_book_wrap {
		position: relative;
		padding-right: 304px;

		.detail_swiper_wrap {
			position: relative;
			width: 680px;
			padding-right: 46px;
			z-index: 2;
			background-color: #fff;
			box-sizing: border-box;

			.swiper-container {
				.swiper-slide {
					width: 100%;
					@include mixins.justify_width(start, center, column);
					background-color: #fff;
					height: auto;
				}
			}

			.prod_area {
				.prod_thumb_box {
					width: 234px;

					.img_box{
						img{
							max-height: calc(234px * 1.618 - 10px);
						}
					}
				}

				.prod_recommend_text, .prod_gift_text {
					margin-top: 30px;
				}
			}
		}

		.thumb_swiper_wrap {
			position: absolute;
			top: 50%;
			right: 0;
			width: 466px;
			@include mixins.transform(translateY(-50%));

			.thumb_list {
				align-items: center;

				.thumb_item {
					width: 142px;
					margin-right: 20px;
					box-sizing: border-box;

					.thumb_img_box {
						width: calc(100% - 2px);
						border: 1px solid variables.$colorGray04;

						img {
							width: 100%;
						}
					}

					&:last-child {
						margin-right: 0;
					}
				}
			}
		}

		.swiper-button-prev {
