.range_slider_wrap{
	position: relative;

	.range_slider{
		height: 12px;
		padding: 4px;
		box-sizing: border-box;

		.range_slider_inner{
			display: block;
			width: 100%;
			height: 4px;
			background-color: variables.$colorGray04;

			.ui-slider-range{
				background-color: variables.$kyoboPurple;
				top: -4px;
				height: 12px;
				@include mixins.transition(width .23s, left .23s);
			}

			.ui-slider-handle{
				top: -13px;
				width: 30px;
				height: 30px;
				margin-left: -15px;
				@include mixins.transition(left .23s);

				&:before{
					content: '';
					position: absolute;
					top: 9px;
					left: 9px;
					display: block;
					width: 12px;
					height: 12px;
					border-radius: 100%;
					background-color: variables.$kyoboPurple;
				}

				&:after{
					content: '';
					position: absolute;
					top: 11px;
					left: 11px;
					display: block;
					width: 8px;
					height: 8px;
					border-radius: 100%;
					background-color: #fff;
				}

				&:hover, &:focus, &:active{
					cursor: pointer;
				}
			}
		}
	}

	.range_slider_val{
		position: absolute;
		width: 100%;
		top: 0;
		left: 0;
		@include mixins.justify_width(start, between);

		.val{
			position: relative;
			width: 12px;
			padding-bottom: 25px;
			text-align: center;
			white-space: nowrap;

			.text{
				position: absolute;
				bottom: 0;
				left: 50%;
				@include mixins.transform(translateX(-50%));
				@include mixins.font_size_xs;
				font-weight: variables.$fontWeightMedium;
				color: variables.$textColorGray;
				@include mixins.transition(color .23s);
			}

			&:before{
				content: '';
				display: block;
				width: 12px;
				height: 12px;
				border-radius: 100%;
				box-sizing: border-box;
				border: 1px solid variables.$colorGray02;
				background-color: #fff;
				@include mixins.transition(all .23s);
			}

			&.active{
				&:before{
					border-color: transparent;
					background-color: transparent;
				}

				.text{
					color: variables.$kyoboPurple;
				}
			}
		}
	}
}

