			tr{
				th{
					padding: 24px 10px;
					@include mixins.font_size_xs;
					font-weight: variables.$fontWeightMedium;
					color: variables.$textColorLightGray;
					border-bottom: 1px solid variables.$colorGray04;
					text-align: left;
				}

				td{
					padding: 24px 14px 24px 15px;
					border-bottom: 1px solid variables.$colorGray04;
					@include mixins.font_size_xs;
					color: variables.$textColorGray;
					text-align: center;

					.btn_text_link{
						color: variables.$textColorBlack;
						text-decoration: underline;
						text-align: center;
					}

					.prod_price{
						font-size: 0;
						line-height: 1;

						.percent {
							display: inline-block;
							margin-right: 3px;
							@include mixins.font_size_xs;
							font-weight: variables.$fontWeightBold;
							color: variables.$kyoboGreen;
							vertical-align: top;
						}

						.price {
							display: inline-block;
							font-size: 0;
							vertical-align: top;

							.val, .unit {
								@include mixins.font_size_xs;
								color: variables.$textColorBlack;
							}

							.val {
								font-weight: variables.$fontWeightBold;
							}
						}

						.price_normal {
							display: inline-block;
							margin: 3px 0 0 2px;
							font-size: 0;
							vertical-align: top;

							.val {
								@include mixins.font_size_min;
								color: variables.$textColorLightGray;
							}
						}

						.point {
							display: block;
							margin-top: 5px;
							@include mixins.font_size_min;
							color: variables.$textColorGray;
							vertical-align: top;
						}
					}

					.review_summary_wrap{
						.review_klover_box{
							display: block;
						}

						.review_quotes_text{
							margin: 5px 0 0;
						}
					}

					&:last-child{
						padding-right: 10px;
					}
				}

				&.prod{
					td{
						padding: {
							top: 30px;
							bottom: 30px;
						};
						vertical-align: top;
						color: variables.$textColorBlack;

						.prod_num{
							display: block;
							min-height: 22px;
							margin-bottom: 12px;
							text-align: center;
						}

						.prod_thumb_box{
							position: relative;
							width: 194px;
							height: 280px;
							margin: 0 auto;

							.img_box{
								display: block;
								border: 1px solid variables.$colorGray04;

								img{
									width: 100%;
									max-height: 278px;
								}
							}

							.btn_prod_delete{
								position: absolute;
								top: 0;
								right: 0;
								width: 22px;
								height: 22px;
								@include mixins.ratio_images('../images/btn_spec_del', 'png', 22px, 22px);
							}
						}

						.prod_btn_wrap{
							@include mixins.justify_width;
							margin-top: 16px;
							font-size: 0;

							.btn_wrap{
								flex: 1;
								margin-left: 2px;
							}
						}

						.prod_compare{
							.btn_prod_compare{
								@include mixins.justify_width(center, center, column);
								width: 194px;
								height: 280px;
								border: 1px dashed variables.$colorGray03;
								background-color: variables.$colorGray06;
								@include mixins.font_size_xxs;
								color: variables.$textColorLightGray;

								&:before{
									content: '';
									position: relative;
									display: block;
									width: 16px;
									height: 16px;
									margin-bottom: 10px;
									@include mixins.ratio_images('../images/ico_prod_compare', 'png', 16px, 16px);
								}
							}

							.btn_sm{
								margin-top: 16px;
								width: 100%;
							}
						}

					}
				}

				&.name{
					td{
						color: variables.$textColorBlack;
					}
				}

				&.date{
					td{
						color: variables.$textColorBlack;
					}
				}

				&.rank{
					td{
						font-weight: variables.$fontWeightBold;
						color: variables.$textColorBlack;
					}
				}

				&:last-child{
					tr{
						&:not(.prod){
							th, td{
								padding-bottom: 40px;
							}
						}
					}
				}
			}
		}
	}
}

