.recommend_freebie_wrap {
	@include mixins.justify_width(center, center, column);
	padding: 50px 20px;
	border-radius: 20px;
	@include mixins.ratio_images('../images/bg_recommend_freebie', 'png', cover);

	.title_wrap {
		@include mixins.justify_width(center, center, column);

		&.has_sub_title {
			.info_text {
				width: auto;
				text-align: center;
				word-break: keep-all;
			}
		}
	}

	.bul_item_asterisk {
		+.btn_wrap {
			margin-top: 40px;
		}
	}

	+.list_result_wrap {
		margin-top: 50px;
	}
}

//  

.freebie_banner {
	position: relative;
	width: 450px;
	margin : 0 auto 10px;

	.freebie_banner_swiper_wrap{
		.freebie_banner_box{
			overflow: hidden;
			border-radius: 20px;

			img{
				width: 100%;
			}
		}
	}

	.swiper-button-prev {
		left: -22px;
	}

	.swiper-button-next {
		right: -22px;
	}

}


.freebie_prod_list {
	width: calc(100% + 36px);
	margin: 0 -18px;
	font-size: 0;

	.freebie_prod_item {
		display: inline-block;
		width: 25%;
		padding: 0 18px;
		box-sizing: border-box;
		vertical-align: top;

		.freebie_prod_area {
			overflow: hidden;
			border: 1px solid variables.$colorGray03;
			border-radius: 20px;

			.freebie_prod_link {
				display: block;
			}

			.freebie_thumb_box {
				border-bottom: 1px solid variables.$colorGray03;

				img {
					width: 100%;
				}
			}

			.freebie_info_box {
				padding: 20px;
				font-size: 0;

				.freebie_name {
					display: block;
					padding-right: 8px;
					@include mixins.font_size_sm;
					font-weight: variables.$fontWeightMedium;
					color: variables.$textColorBlack;
					@include mixins.ellipsis;
				}

				.freebie_introduction {
					display: block;
					margin-top: 5px;
					@include mixins.font_size_xxs;
					color: variables.$textColorGray;
				}

				.freebie_date {
					display: block;
					margin-top: 10px;
					@include mixins.font_size_xxs;
					color: variables.$textColorGray;
				}

				.freebie_info_list {
					margin-top: 12px;

					.freebie_info_item {
						@include mixins.justify_width(start);

						.freebie_title {
							flex-shrink: 0;
							display: inline-block;
							@include mixins.font_size_xxs;
							font-weight: variables.$fontWeightMedium;
							color: variables.$textColorBlack;
							vertical-align: top;
						}

						.gap {
							display: inline-block;
							width: 1px;
							height: 7px;
							margin: 5px 8px 0 5px;
							background-color: variables.$colorGray02;
							font-size: 0;
							vertical-align: top;
						}

						.freebie_desc {
							display: inline-block;
							@include mixins.font_size_xxs;
							font-weight: variables.$fontWeightMedium;
							color: variables.$textColorGray;
							vertical-align: top;
						}

						+.freebie_info_item {
							margin-top: 5px;
						}
					}
				}

			}

			.freebie_bottom_box {
				padding: 0 20px 20px;


				.freebie_info_inner {
					padding-top: 15px;
					@include mixins.justify_width(center);
					border-top: 1px solid variables.$colorGray04;

					.freebie_want_text {
						font-size: 0;

						.want_text {
							display: inline-block;
							margin-top: 2px;
							@include mixins.font_size_xxs;
							font-weight: variables.$fontWeightMedium;
							color: variables.$textColorPurple;
							vertical-align: top;
						}

						.btn_like {
							margin-left: 5px;
						}
					}

					.right_area {
						margin-left: auto;
					}

					.sns_share_wrap{
						width: 20px;
						height: 20px;

						.sns_share_box{
							margin-top: -8px;
						}
					}

					.btn_share {
						width: 20px;
						height: 20px;
						border: none;

						.ico_share {
							width: 20px;
							height: 20px;
							margin: 0;
							background: {
								position: center;
								repeat: no-repeat;
							}
							@include mixins.ratio_images('../images/ico_share_lg', 'png', 20px, 20px);
						}
					}
				}

			}
		}

		&:nth-child(n+5) {
			margin-top: 36px;
		}
	}
}





