@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';

// 
.prod_area {
	&.horizontal {
		.prod_info_box{
			.prod_realtime {//Picks >  SCR-BIZ15-15-P801
				margin-top: 20px;
				display: inline-block;
				@include mixins.font_size_xxs;
				color: variables.$kyoboGreen;
				font-weight: variables.$fontWeightBold;
				vertical-align: top;
				
				&:before {
					content: '';
					display: inline-block;
					width: 18px;
					height: 18px;
					background: {
						position: center;
						repeat: no-repeat;
					}
					margin-right: 6px;
					@include mixins.ratio_images('../images/ico_cart_h18_green', 'png', 18px, 18px);
					vertical-align: top;
					@include mixins.transition(background 0.2s ease-out);
				}
			}
			
			.prod_picks_selected {
				margin-top: 30px;
				text-align: left;
				
				.btn_sm {
					width: 146px;
					
					+.btn_sm {
						margin-top: 0;
						margin-left: 5px;
					}
				}
			}
		}
	}

	.prod_picks_selected {
		margin-top: 26px;
		text-align: center;
		
		.btn_sm {
			width: 146px;
			
			[class^=ico_pick_] {
				display: inline-block;
				width: 18px;
				height: 18px;
				margin-right: 2px;
			}
			
			.ico_pick_good{
				@include mixins.ratio_images('../images/ico_picto_good_green', 'png', 18px, 18px);
			}
			.ico_pick_not_good{
				@include mixins.ratio_images('../images/ico_picto_not_good_black', 'png', 18px, 18px);
			}
			
			+.btn_sm {
				margin-top: 6px;
			}
		}
	}
	
}


.wrapper.contents_picks {
	
	.picks_book_recommend_box {
		position: relative;
		display: block;
		width: 100%;
		padding: 40px;
		border: 1px solid variables.$colorGray04;
		border-radius: 20px;
		box-sizing: border-box;
		@include mixins.justify_width(center, center);
		
		.book_info_area {
			padding-right: 30px;
			
			.prod_introduction {
				margin-top: 12px;
			}
			
			.prod_bottom {
				margin-top: 20px;
			}
		}//book_info_area
		
		.book_info_right_area {
			position: relative;
			flex-shrink: 0;
			width: 417px;
			margin-left: auto;
			padding-left: 31px;
			box-sizing: border-box;
			
			.title_wrap {
				&.title_size_xs {
					padding-bottom: 12px;
					
					~.title_size_xs {
						margin-top: 24px;
					}
				}
			}
			
			.charting_area {
				@include mixins.justify_width(center);
				
				.circle_progress_box {
					position: relative;
					width: 56px;
					
					.name {
						display: block;
						@include mixins.font_size_xxs;
						font-weight: variables.$fontWeightRegular;
						color: variables.$textColorBlack;
						text-align: center;
						margin-top: 10px;
						margin-left: -5px;
						margin-right: -5px;
					}
					
					.circle_progress {
						margin: 0 auto;
						transform: rotate(-90deg);
					}
					.frame, .bar { fill: none; }
					.frame { stroke: rgba(variables.$kyoboGreen, .2); }
					.bar {
						stroke: variables.$kyoboGreen;
						stroke-linecap: round;
					}
					.value {
						display: inline-block;
						position: absolute;
						left: 0;
						right: 0;
						top: 19px;
						text-align: center;
						line-height: 19px;
						
						.val {
							font-size: variables.$fontSizeDef;
							letter-spacing: variables.$letterSpacing;
							font-weight: variables.$fontWeightBold;
							color: variables.$textColorBlack;
						}
						.label {
							font-size: variables.$fontSizeMin;
							letter-spacing: variables.$letterSpacing;
							color: variables.$textColorGray;
						}
					}
					
					+ .circle_progress_box {
						margin-left: 24px;
					}
				}
				
				
			}
			
			
		}//book_info_right_area
		
		
		+.picks_book_recommend_box {
			margin-top: 30px;
		}
	}
	
}

.keyword_pick {
	padding-bottom: 30px;
	
	.keyword_pick_header {
		@include mixins.justify_width(center, start);
		
		.logo_area {
			@include mixins.justify_width(center);
			font-size: 0;
			line-height: 0;
			.text {
				@include mixins.font_size_md;
				font-weight: variables.$fontWeightBold;
			}
			
			.ico_logos_pick {
				display: inline-block;
				width: 51px;
				height: 21px;
				margin-left: 8px;
				@include mixins.ratio_images('../images/logo_picks_header_black', 'png', 51px, 21px);
			}
		}
		
		.search_area {
			margin-left: 20px;
			
			.keyword_pick_search_box {
				@include mixins.justify_width(center);
				width: 560px;
				height: 48px;
				padding-right: 14px;
				border-radius: 25px;
				background: #fff;
				border: 1px solid variables.$kyoboPurple;
				box-sizing: border-box;
				@include mixins.transition(width .23s variables.$transEaseBounceBack);
				
				.search_input_wrap {
					position: relative;
					flex: 1;
					height: 100%;
					padding-left: 16px;
					
					
					.ip_keyword_pick_search {
						width: 100%;
						height: 100%;
						background: none;
						outline: none;
						@include mixins.font_size_xs;
						@include mixins.set_placeholder_color(variables.$textColorLightGray);
						
						&::-webkit-search-decoration, &::-webkit-search-cancel-button, &::-webkit-search-results-button, &::-webkit-search-results-decoration {
							display: none;
							width: 0;
							height: 0;
						}
						
					}
					
					input[type=search]::-ms-clear {
						display: none;
						width: 0;
						height: 0;
					}
					
					input[type=search]::-ms-reveal {
						display: none;
						width: 0;
						height: 0;
					}
					
					.btn_ip_clear {
						display: none;
						position: absolute;
						right: 10px;
						top: 50%;
						width: 20px;
						height: 20px;
						@include mixins.transform(translateY(-50%));
						background: {
							position: center;
							repeat: no-repeat;
						}
						@include mixins.ratio_images('../images/btn_ip_reset', 'png', 18px, 18px);
					}
					
					&.value {
						.ip_gnb_search {
							padding-right: 40px;
						}
						
						.btn_ip_clear {
							display: block;
						}
					}
				}
				
				.btn_keyword_pick_search {
					display: block;
					width: 30px;
					height: 30px;
					background: {
						position: center;
						repeat: no-repeat;
					}
					@include mixins.ratio_images('../images/btn_header_search', 'png', 18px, 18px);
				}
			}
			
		}

		.right_area {
			margin-left: auto;
		}
	}//keyword_pick_header
	.keyword_pick_body {
		margin-top: 30px;
		
		.keyword_pick_info_box {
			@include mixins.justify_width(start, center);
			position: relative;
			display: block;
			width: 100%;
			padding: 30px;
			background: rgba(variables.$kyoboGreen, .1);
			border-radius: 20px;
			box-sizing: border-box;
			
			&:before {
				content: '';
				position: relative;
				display: inline-block;
				width: 80px;
				height: 80px;
				margin-right: 30px;
				@include mixins.ratio_images('../images/ico_picks_keyword_pick_green', 'png', 80px, 80px);
			}

			.right_area {
				display: inline-block;
				.info_group {
					+.info_group {
						margin-top: 10px;
					}
				}
				
				.text {
					display: block;
					@include mixins.font_size_xxs;
					
					.fc_green {
						font-weight: variables.$fontWeightBold;
						color: variables.$kyoboGreen;
					}
				}
			}
			
			
			
			+.picks_custom_pick_result_wrap {
				margin-top: 50px;
			}
			
		}//keyword_pick_info_box
		
		.keyword_pick_list_wrap {
			margin-top: 30px;
		}//keyword_pick_info_box
		
		.keyword_pick_prod_wrap {
			margin-top: 30px;
			
			.no_data {
				&.size_sm {
					padding-top: 150px;
				}
			}
			
			.prod_list {
				.prod_item {
					display: inline-block;
					width: 400px;
					margin-left: 36px;
					vertical-align: top;
					
					&:nth-child(6n+1) {
						margin-left: 0;
					}
					
					&:nth-child(n+7) {
						margin-top: 24px;
					}
					&:nth-child(2n+1) {
						margin-left: 0;
					}
					
					&:nth-child(n+3) {
						margin-top: 24px;
					}
					
					.prod_area {
						@include mixins.justify_width(start);
					}
				}
			}
		}
		.tag_wrap {
			.tag {
				&.active {
					background-color: variables.$textColorGray;
					border-color: variables.$textColorGray;
					color: #fff;
				}
			}
		}
		
	}//keyword_pick_body

}

