			@include mixins.justify_width(center);
			@include mixins.font_size_lg;
			font-weight: variables.$fontWeightBold;
			color: #fff;
			
			.text {
				.name {
					//   ()
				}
			}
			
			+.picks_header_desc {
				margin-top: 10px;
			}
			
			.ico_logos_pick {
				display: inline-block;
				width: 64px;
				height: 30px;
				margin-left: 8px;
				@include mixins.ratio_images('../images/logo_picks_header', 'png', 64px, 30px);
			}
		}
		.picks_header_desc {
			@include mixins.font_size_sm;
			font-weight: variables.$fontWeightRegular;
			color: #fff;
		}
		
		.btn_md {
			margin-top: 24px;
			font-size: variables.$fontSizeXs;
			font-weight: variables.$fontWeightRegular;
		}
	}
	
	

}

.picks_body_area {
	padding-top: 50px;
	
	.picks_body_top_wrap {
		margin-top: 50px;
		padding-bottom: 10px;
		
		.title_wrap {
			&.size_md.has_sub_title {
				.info_text {
					margin-top: 10px;
				}
			}
		}
	}//picks_body_top_wrap
	
	.picks_mypicks_wrap {
		border-top: solid 1px variables.$colorGray04;
		padding-top: 40px;
		
		
		.picks_mypicks_contents {
			
			@include mixins.justify_width(center);
			.picks_type_box {
				width: 479px;
				
				.picks_type_card {
					position: relative;
					padding: 40px;
					height: 300px;
					border-radius: 20px;
					box-sizing: border-box;
					box-shadow: 5px 5px 15px rgba(variables.$textColorBlack, 0.06);
					background-position: top left;
					background-size: 100%;
					
					.character {
						display: block;
						@include mixins.font_size_lg;
						font-weight: variables.$fontWeightBold;
						color: #fff;
					}
					.desc {
						display: block;
						margin-top: 10px;
						@include mixins.font_size_def;
						font-weight: variables.$fontWeightRegular;
						color: #fff;
					}
					
					.badge_xl {
						position: absolute;
						top: 24px;
						right: 24px;
						display: inline-block;
						padding : {
							top: 5px;
							bottom: 5px;
							left: 10px;
							right: 10px;
						}
						box-shadow: 0 5px 10px rgba(variables.$textColorBlack, 0.4);
						font-size: variables.$fontSizeXs;
						line-height: variables.$fontSizeXs;
						font-weight: variables.$fontWeightBold;
						color: #fff;
						
						&.badge_pill {
							border-radius: 19px;
						}
						
						&.badge_green {
							color: #fff;
							background: variables.$kyoboGreen;
							border: 1px solid variables.$kyoboGreen;
						}
					}
				}
				
				.picks_type_desc {
					//        css   (       7px )
					position: relative;
					height: 42px;
					margin-top: 20px;
					background-color: #f5f1f6;
					border-radius: 10px;
					padding: 19px 20px 17px;
					@include mixins.font_size_xs;
					
					
					&::before {
						content: '';
						position:absolute;
						top: 0;
						left: 30px;
						width: 14px;
						height: 14px;
						background-color: #f5f1f6;
						transform:rotate(315deg) translate(0, -50%);
					}
				}
				
				&.bg_purple {
					.picks_type_card { background-color: #5E1D6E; }
					.picks_type_desc {
						background-color: #f5f1f6;
						&::before { background-color: #f5f1f6;}
						.fw_bold { color: #5E1D6E; }
					}
				}
				
			}//picks_type_box
			
			.picks_type_analysis_box {
				width: 685px;
				height: 400px;
				border-radius: 20px;
				border: solid 1px variables.$colorGray04;
				box-sizing: border-box;
				margin-left: auto;
				
				
				.picks_type_analysis_swiper_wrap {
					position: relative;
					height: 100%;
					
					.swiper_control_box {
						position: absolute;
						bottom: 34px;
						left: 0;
						right: 0;
					}
					
					.swiper-button-prev,
					.swiper-button-next {
						position: absolute;
						top: auto;
						left: auto;
						right: 20px;
						bottom: 20px;
						margin-top: 0;
					}
					.swiper-button-prev {
						right: 64px;
					}
				}
				
				.picks_type_analysis_area {
					height: 398px;
					padding: 40px;
					box-sizing: border-box;
					
					.title_wrap{
						width: auto;
						&.title_size_md {
							padding-bottom: 30px;
							
							.title_heading {
								font-weight: variables.$fontWeightRegular;
							}
						}
					}
					
					.picks_type_preference_box {
						.picks_type_preference_item {
							@include mixins.justify_width(center, center);
							
							> .label {
								flex-shrink: 0;
								width: 110px;
								@include mixins.font_size_xs;
							}
							.charting_area {
								.bar_progress_box {
									
									.frame {
										width: 450px;
										font-size: 0;
										line-height: 0;
										
										.bar {
											position: relative;
											display: inline-block;
											height: 8px;
											border-radius: 6px;
											
											.value {
												position: absolute;
												display: inline-block;
												top: -3px;
												left: calc(100% + 12px);
												font-size: variables.$fontSizeXxs;
												font-weight: variables.$fontWeightBold;
												line-height: 1;
												letter-spacing: variables.$letterSpacing;
												word-break: keep-all;
											}
										}
										
										&.green {
											.bar { background-color: variables.$kyoboGreen; }
											.value { color: variables.$kyoboGreen; }
										}
										&.purple {
											.bar { background-color: variables.$kyoboPurple; }
											.value { color: variables.$kyoboPurple; }
										}
									}
									
									+.bar_progress_box{
										margin-top: 14px;
									}
								}
							}
							+.picks_type_preference_item {
								margin-top: 33px;
							}
						}//picks_type_preference_item
					}//picks_type_preference_box
					
					.picks_type_preference_legend_box {
						margin-top: 30px;
						
						.legend_text {
							position: relative;
							display: inline-block;
							@include mixins.font_size_xxs;
							
							&:before {
								content: '';
								display: inline-block;
								width: 10px;
								height: 10px;
								border-radius: 10px;
								margin-right: 8px;
								background-color: variables.$colorGray03;
							}
							
							&.green {
								&:before { background-color: variables.$kyoboGreen; }
							}
							&.purple {
								&:before { background-color: variables.$kyoboPurple; }
							}
							
							+ .legend_text {
								margin-left: 20px;
							}
						}
					}//picks_type_preference_legend_box
					
					.picks_type_author_recommend_box {
						.picks_type_author_recommend_list {
							margin-right: -24px;
							
							.picks_type_author_recommend_item {
								display: inline-block;
								width: calc(50% - 24px);
								margin-left: 24px;
								vertical-align: top;
								box-sizing: border-box;
								
								&:nth-child(2n+1){
									margin-left: 0;
								}
								
								&:nth-child(n+3){
									margin-top: 24px;
								}
								
								.person_info_area {
									border:0;
									padding: 0;
									
									.person_profile_box {
										.profile_info {
											flex: auto;
										}
									}
								}
								
							}
						}
					}
					
				}//picks_type_analysis_area
				
			
			}//picks_type_analysis_box
		}
		
	}//picks_mypicks_wrap
	
	.picks_synthesis_picks_wrap {
		
		.picks_book_recommend_box {
			
			.book_info_area {
				width: calc(100% - 417px)
			}
			
			&:before {
				content: '';
				position: absolute;
