							border: 1px solid variables.$colorGray02;
							border-radius: 100%;
							box-sizing: border-box;
							background-color: transparent;
							background: {
								position: center;
								repeat: no-repeat;
								color: transparent;
							}
							@include mixins.ratio_images('../images/btn_swiper_arr_gray_sm', 'png', 34px, 34px);
							
							&.prev {
								@include mixins.transform(rotate(180deg));
							}
