						}
					}
				}
			}
		}
		
		.picks_preference_author_wrap {
			border-radius: 20px;
			background: #F1EBE9;
			
			.person_info_area {
				position: relative;
				@include mixins.justify_width(center);
				padding: 40px 50px 40px 40px;
				border:0;
				
				.person_profile_box {
					width: 616px;
					
					.profile_img {
						width: 118px;
						height: 118px;
					}
					.profile_info {
						width: 468px;
						margin-left: 30px;
						.person_name {
							.name {
								@include mixins.font-size_md;
								font-weight: variables.$fontWeightBold;
							}
							.btn_wish_icon {
								margin-top: 3px;
							}
						}
						.person_genre {
							@include mixins.justify_width(center);
							margin-top: 12px;
							font-size: 0;
							line-height: 0;
							.genre {
								display: inline-block;
								@include mixins.font_size_xxs;
								font-weight: variables.$fontWeightMedium;
								color: variables.$textColorBlack;
								
							}
							
							.arw_genre {
								display: inline-block;
								width: 6px;
								height: 22px;
								font-size: 0;
								line-height: variables.$fontSizeXsLineHeight;
								background: {
									position: center;
									repeat: no-repeat;
								}
