.gift_rank_prod_wrap {
	@include mixins.clearfix;
	position: relative;
	width: 100%;
	
	//btn_pagination_next@2x
	.gift_rank_thumb_wrap {
		float: left;
		width: calc(100% - 824px);
		height: 386px;
		box-sizing: border-box;
		padding-bottom: 36px;
		
		.mask {
			position: relative;
			display: block;
			overflow: hidden;
			width: 100%;
			height: 350px;
		}
		.gift_rank_thumb_list {
			position: absolute;
			display: block;
			overflow: hidden;
			width: 100%;
			margin-bottom: 16px;
			
			.gift_rank_thumb_item {
				
				width: 100%;
				height: 70px;
				
				+.gift_rank_thumb_item {
					.btn_gift_rank_thumb {
						border-top-color: transparent;
					}
				}
				&:nth-child(5n+1) {
					.btn_gift_rank_thumb {
						border-top-color: variables.$colorGray02;
					}
				}
				&.prev_active {
					&:nth-child(5n){
						.btn_gift_rank_thumb {
							border-bottom-color: variables.$colorGray02;
						}
					}
					.btn_gift_rank_thumb {
						border-bottom-color: transparent;
					}
				}
				
				.btn_gift_rank_thumb {
					text-align: left;
					width: 100%;
					height: 70px;
					padding: 0 20px;
					@include mixins.justify_width(center, center);
					box-sizing: border-box;
					border-top: 1px solid variables.$colorGray02;
					border-right: 1px solid transparent;
					border-left: 1px solid transparent;
					border-bottom: 1px solid variables.$colorGray02;
				}
				
				.left_area {
					display: inline-block;
					font-size: 0;
					
					.rank {
						display: inline-block;
						width: 17px;
						margin-right: 12px;
						color: variables.$textColorGray;
						@include mixins.font_size_sm;
						font-weight: variables.$fontWeightRegular;
						vertical-align: top;
					}
					
					.name {
						width: 260px;
						display: inline-block;
						@include mixins.font_size_sm;
						@include mixins.ellipsis(false, true, false);
						color: variables.$textColorLightGray;
						font-weight: variables.$fontWeightRegular;
					}
				}
				
				.right_area {
					display: inline-block;
					margin-left: auto;
				}
				
				
				&.active {
					.btn_gift_rank_thumb {
						border-color: #000;
						border-radius: 10px;
					}
					.left_area {
						.rank {
							color: variables.$textColorGreen;
							font-weight: variables.$fontWeightMedium;
						}
						.name {
							color: variables.$textColorBlack;
							font-weight: variables.$fontWeightMedium;
						}
					}
				}
			}
		}
		
		.btn_wrap {
			margin-top: 12px;
			@include mixins.justify_width(center);
			.left_area {
				display: inline-block;
			}
			.right_area {
				display: inline-block;
				margin-left: auto;
			}
			
			
			.btn_rank_arw {
				display: inline-block;
				width: 24px;
				height: 24px;
				border-radius: 12px;
				text-align: center;
				font-size: 13px;
				letter-spacing: variables.$letterSpacing;
				vertical-align: top;
				
				&.prev, &.next {
					background: {
						position: center;
						repeat: no-repeat;
					}
				}
				
				&.prev {
					@include mixins.ratio_images('../images/btn_pagination_prev', 'png', 24px, 24px);
				}
				
				&.next {
					@include mixins.ratio_images('../images/btn_pagination_next', 'png', 24px, 24px);
				}
			}
		}
	}
	
	.gift_rank_prod_detail_swiper_wrap {
		display: inline-block;
		width: 790px;
		//height: 433px;
		margin-top: 33px;
		margin-left: 34px;
		margin-right: auto;
		
		.swiper-slide {
			background-color: #fff;
			
			.prod_area {
				@include mixins.justify_width(start, start);
				padding-left: 80px;
				
				&:before {
					content: '';
					position: absolute;
					width: 350px;
					height: 350px;
					left: 0;
					bottom: 0;
					border-radius: 50%;
					background-color: variables.$colorGray06;
				}
				
				.prod_thumb_box {
					width: 320px;
					height: auto;
					
					img {
						max-height: none;
					}
					
					&.type_square_round {
						border-radius: 20px;
					}
				}
				
				.prod_info_box {
					width: 330px;
					margin-left: 60px;
					padding-top: 15px;
					
					.badge_sm {
						+.prod_info {
							margin-top: 8px;
						}
					}
					
					.prod_name {
						@include mixins.font_size_md;
						max-height: 60px;
					}
					.prod_author {
						margin-top: 8px;
						@include mixins.font_size_xs;
					}
					
					.review_summary_wrap {
						margin-top: 30px;
						.review_quotes_text {
							margin-top: 0;
						}
					}
					.prod_introduction {
						margin-top: 20px;
					}
					
					.prod_showing {
						margin-top: 30px;
					}
					
					.btn_wrap {
						margin-top: 30px;
						text-align: left;
						
						.btn_md {
							font-weight: variables.$fontWeightRegular;
							
							&.btn_wish {
								width: 44px;
								min-width: 44px;
								padding:0;
								
								.ico_wish {
									margin: 0;
								}
							}
						}
						
						
					}
				}
				
			}
			
			
		}
	}
	
	
